"use client";

import { useRouter } from "next/navigation";
import { Box, Button, IconButton, Tooltip } from "@mui/material";
import { motion } from "framer-motion";
import CustomTable from "@/app/components/CustomTable";
import { FiPlus } from "react-icons/fi";
import { ArrowForwardIos, Person, Star } from "@mui/icons-material";
import * as MuiIcons from "@mui/icons-material";
import React, { useEffect, useState } from "react";
import { getApi } from "@/utils/getApiMethod";

export default function VehicleList() {
    const router = useRouter();

    const vehicleData = [];

    const [columns, setColumns] = useState([]);

    useEffect(() => {
        const fetchColumns = async () => {
            const result = await getApi("/fieldindex01/table?entity_name=Vehicle");
            console.log("result", result);

            const dynamicColumns = result.data.map((col) => ({
                key: col.key,
                label: col.label,
                icon: col.icon ? React.createElement(MuiIcons[col.icon], { fontSize: "small" }) : null
            }));

            // Merge action column
            const actionColumn = {
                key: "actions",
                label: "Actions",
                icon: <MuiIcons.Settings fontSize="small" />,
                render: (row) => (
                    <Box sx={{ display: "flex", gap: 1 }}>
                        <Tooltip title="Edit">
                            <IconButton size="small">
                                <MuiIcons.EditOutlined fontSize="small" />
                            </IconButton>
                        </Tooltip>
                        <Tooltip title="Delete">
                            <IconButton size="small">
                                <MuiIcons.DeleteOutlineOutlined fontSize="small" />
                            </IconButton>
                        </Tooltip>
                    </Box>
                )
            };

            setColumns([...dynamicColumns, actionColumn]);
        };

        fetchColumns();
    }, []);

    return (
        <motion.div
            initial={{ opacity: 0, scale: 0.9, x: -20 }}
            animate={{ opacity: 1, scale: 1, x: 0 }}
            exit={{ opacity: 0, scale: 0.9, x: 20 }}
            transition={{ duration: 1, ease: [0.4, 0, 0.2, 1] }}
        >
            <Box sx={{ display: "flex", flexDirection: "column" }}>
                {/* Header */}
                <div className="flex items-center">
                    <h4 className="ml-2 text-md font-semibold !text-gray-400">Vehicle List</h4>
                </div>

                {/* Filter / Tabs bar */}
                <div className="flex items-center justify-between">
                    {/* Left Tabs */}
                    <div className="flex items-center">
                        <div className="tab-item">
                            <Star fontSize="small" />
                            <span>All Tasks</span>
                        </div>

                        <div className="tab-item">
                            <ArrowForwardIos fontSize="inherit" style={{ fontSize: 14 }} />
                            <span>By Status</span>
                        </div>

                        <div className="tab-item">
                            <Person fontSize="small" />
                            <span>My Tasks</span>
                        </div>
                    </div>

                    {/* Right Button */}
                    <Button
                        className="btn-primary"
                        sx={{ textTransform: "none" }}
                        onClick={() => router.push("/dashboard/vehicle-master/add")}
                        startIcon={<FiPlus style={{ fontSize: 16 }} />}
                    >
                        Add Vehicle
                    </Button>
                </div>

                {/* Table Container */}
                <CustomTable
                    columns={columns}
                    data={vehicleData}
                />
            </Box>
        </motion.div>
    );
}
